* {
  font-family: 'Calibri', sans-serif;
}

.app-container {
  max-width: 100%;
  height: 100vh;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  position: relative;
  overflow: hidden;
}

h1 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.input-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  position: relative;
}

textarea {
  width: 250px;
  height: 80px;
  padding: 10px;
  font-size: 1rem;
}

button {
  padding: 8px 16px;
  font-size: 1rem;
  background-color: #aee0ee;
  border: none;
  cursor: pointer;
  border-radius: 20px;
  margin-top: 10px;
}

button:hover {
  background-color: #99d7e9;
}

.notes-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.note {
  width: 150px;
  height: 150px;
  padding: 20px;
  background-color: #fff492;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  position: absolute;
  cursor: move;
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.note p {
  margin: 0;
  font-size: 1rem;
  word-wrap: break-word;
  white-space: pre-wrap;
  max-height: 100%;
  overflow: hidden;
}

.delete-button {
  position: absolute;
  top: -5px;
  right: 0px;
  background-color: transparent;
  border: none;
  width: 24px;
  height: 24px;
  border-radius: 100%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.delete-button::before,
.delete-button::after {
  content: '';
  position: absolute;
  width: 12px;
  height: 1.5px;
  background-color: #e8c827;
}

.delete-button::before {
  transform: rotate(45deg);
}

.delete-button::after {
  transform: rotate(-45deg);
}

.delete-button:hover {
  background-color: transparent;
}

.delete-button:hover::before,
.delete-button:hover::after {
  background-color: #d3a500;
}
